import helper from '../../common/helper.js';
export default {
	data() {
		return {
			userfoArray:'',
			AmountArray: [],
			paymentid:'',
			Id: '',
			type:"",
			current: '1',
			items: [{
					value: '1',
				}, {
					value: '2',
				},
				{
					value: '3',
				},
			]
		};
	},
	onLoad() {
		this.userinfo()
	},
	onShow() {

	},
	methods: {
		open(e) {;
			this.Id = e.currentTarget.id
		},
		// 选择支付平台
		radioChange: function(evt) {
			this.type=evt.target.value
		},
		// 获取金额列表
		userinfo() {
			uni.request({
				url: helper.websiteUrl + 'yiru/sdl/translation/gettranslation',
				method: 'GET',
				data: helper.postdata({
				}),
				success: res => {
					 console.log(res)
					if (res.data.code == 200) {
						this.userfoArray = res.data.data.Member;
						
						this.AmountArray = res.data.data.baseinfo;
						this.paymentid = res.data.data.baseinfo.ID;
					} else {
						uni.showToast({
							icon: 'none',
							title: '网络异常,请稍后重试'
						});
					}
				},
				fail: () => {
					uni.showToast({
						icon: 'none',
						title: '网络异常,请稍后重试'
					});
				}
			});
		},
		tixian() {
			if (this.Id == '') {
				uni.showToast({
					icon: 'none',
					title: '请选择提现金额！'
				});
			} else {
				uni.request({
					url: helper.websiteUrl + '/withdrawals/savewithdrawals',
					method: 'GET',
					data: helper.postdata({
						"userid": helper.getstate().userid,
						"type": "2",
						"paymentid": this.Id,
					}),
					success: res => {
						if (res.data.code == 200) {
							this.liebiao()
							uni.showToast({
								icon: 'none',
								title: res.data.info
							});
							this.userinfo()
						} else {
							helper.goout(res.data.info);
							uni.showToast({
								icon: 'none',
								title: res.data.info
							});
						}
					},
					fail: () => {
						uni.showToast({
							icon: 'none',
							title: '网络异常,请稍后重试'
						});
					}
				});
			}
		},
		
		recharge(){
			console.log("充值")
			if(this.Id==''){
				uni.showToast({
					icon: 'none',
					title: '请选择充值金额！'
				});
			}else if(this.type==''){
				uni.showToast({
					icon: 'none',
					title: '请选择支付平台！'
				})
			}else{
				
				uni.request({
						url: helper.websiteUrl + 'yiru/sdl/translation/savetranslation',
						method: 'GET',
						data: helper.postdata({
							"tid": this.Id,
							"type": this.type,
							"mid":helper.getstate().userid,
						}),
						success: res => {
							if (res.data.code == 200) {
								this.liebiao()
								uni.showToast({
									icon: 'none',
									title: res.data.info
								});
								
							} else {
								uni.showToast({
									icon: 'none',
									title: res.data.info
								});
							}
							this.Id='',
							this.type=''
							
						},
						fail: () => {
							uni.showToast({
								icon: 'none',
								title: '网络异常,请稍后重试'
							});
						}
					});
				
			}
			
			
			}
		
	}
	
}
